import React from "react";
import Image from "next/image";

const Activities: React.FC = () => {
  return (
    <section
      className="relative min-h-screen bg-fixed bg-cover bg-center py-12"
      style={{
        backgroundImage: "url('/activities/bg.jpg')",
      }}
    >
      {/* Dark Overlay for better visibility of content on top of background */}
      <div className="absolute inset-0 bg-black/40 z-0"></div>

      <div className="container mx-auto px-4 relative z-10 max-w-6xl">
        <h1 className="text-4xl md:text-5xl font-bold text-white text-center mb-12 drop-shadow-lg">
          Project Activities
        </h1>

        {/* 1. First Image (Full Width) */}
        <div className="mb-12 bg-white/10 backdrop-blur-sm p-4 rounded-2xl shadow-xl border border-white/20">
          <div className="relative w-full h-[400px] md:h-[500px] rounded-xl overflow-hidden">
            <Image
              src="/activities/activities1.avif"
              alt="Main Activity Overview"
              fill
              className="object-cover hover:scale-105 transition-transform duration-500"
            />
          </div>
        </div>

        {/* Implementation Header */}
        <div className="mb-8 text-center">
          <h2 className="text-3xl md:text-4xl font-bold text-white drop-shadow-md bg-orange-600/90 inline-block px-8 py-2 rounded-full backdrop-blur-md">
            Implementation
          </h2>
        </div>

        {/* 2. Second Image (Full Width below header) */}
        <div className="mb-12 bg-white/10 backdrop-blur-sm p-4 rounded-2xl shadow-xl border border-white/20">
          <div className="relative w-full h-[400px] md:h-[500px] rounded-xl overflow-hidden">
            <Image
              src="/activities/activities2.avif"
              alt="Implementation Phase"
              fill
              className="object-cover hover:scale-105 transition-transform duration-500"
            />
          </div>
        </div>

        {/* 3. Third & Fourth Images (Side by Side) */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* Activity 3 */}
          <div className="bg-white/10 backdrop-blur-sm p-3 rounded-2xl shadow-xl border border-white/20">
            <div className="relative w-full h-[300px] rounded-xl overflow-hidden">
              <Image
                src="/activities/activities3.avif"
                alt="Activity Detail 1"
                fill
                className="object-cover hover:scale-105 transition-transform duration-500"
              />
            </div>
          </div>

          {/* Activity 4 */}
          <div className="bg-white/10 backdrop-blur-sm p-3 rounded-2xl shadow-xl border border-white/20">
            <div className="relative w-full h-[300px] rounded-xl overflow-hidden">
              <Image
                src="/activities/activities4.jpg"
                alt="Activity Detail 2"
                fill
                className="object-cover hover:scale-105 transition-transform duration-500"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Activities;